
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tabGroup from '@/components/group/tab'
import {addEvaluation} from '@/api/evaluation'
export default {
components: {
XHeader,
Tab,
TabItem,
XProgress,
Rater,
Alert,
XTextarea,
Group,
tabGroup: tabGroup
},
data () {
return {
a: '<embed src="' + require('./star.svg') + '">',
title: '发表评价',
describle: '退出后编辑过的内容将不保存',
show: false,
placeholder: '输入评价内容',
title1: '发表评价',
ab: '提交',
content:null,
expressScore:null,
productScore:null,
serviceScore:null,
orderId:''
}
},
props: {},
created(){
this.orderId=this.$route.query.orderId;
},
methods: {
getback () {
this.show = true
},
onhide () {
this.$router.push('/myOrder')
},
submitAssess(){
let params={
content:this.content,
orderId:this.orderId,
userId:this.$store.getters.userInfo.userId,
productScore:this.productScore,
expressScore:this.expressScore,
serviceScore:this.serviceScore
}
if(this.productScore!=null&&this.expressScore!=null&&this.serviceScore!=null&&this.content!=null){
addEvaluation(params).then(res=>{
if(res.code==200){
this.$vux.toast.show({
text:'评价成功'
})
this.$router.go(-1);
}
})
}else{
this.$vux.toast.text('请评分完毕后再提交吧')
}



}
}
}



